.giftSetting{
  padding: 70px 28px;
}
.giftSetting .btn-red, .giftSetting .btn-gray{
  position: unset;
}
.gs-paymentgs{
  margin-bottom: 62px;
}
.gs-paymentgs .card-header, .gs-paymentgs .card-block{
  text-align: center;
}
.gs-paymentgs .card-header .title p{
  margin-left: 0;
  padding: 0 16px;
}
.gs-paymentgs .card-block{
  padding: 30px 74px 46px;
}
.gs-paymentgs .pgs-detail .red-color{
  margin-bottom: 38px;
}
.gs-paymentgs .pgs-detail .red-color:first-child{
  margin-bottom: 0;
}
.gift-wrapper .gift-addBtn{
  text-align: right;
  margin-bottom: 18px;
}
.gift-wrapper .gift-addBtn .btn-red{
  margin-left: 16px;
  width: 106px;
}
.gift-wrapper .card-header{
  display: -ms-flexbox;
  display: flex;
}
.gift-wrapper .card-header .title, .gift-wrapper .card-block .go-detail{
  min-width: 82%;
  border-right: 2px solid #f2f2f2;  
}
.gift-wrapper .card-header .activeStatus, .gift-wrapper .card-block .go-as{
  min-width: 18%;
  text-align: center;
}
.gift-wrapper .card-block{
  display: -ms-flexbox;
  display: flex;
  padding: 0;
}
.gift-wrapper .card-header .title p{
  margin-left: 36px;
}
.gift-wrapper .go-detail ul, .gift-wrapper .card-block .go-as ul{
  padding-left: 0;
  margin-bottom: 0;
}
.gift-wrapper .go-detail ul li, .gift-wrapper .card-block .go-as ul li{
  list-style: none;
  padding: 10px 0;
  color: #4d4d4d;
  font-size: 18px;
}
.gift-wrapper .card-block .go-as ul{
  padding: 4px 0;
}
.gift-wrapper .card-block .go-as ul li{
  padding: 14px 0;
}
.gift-wrapper .card-block .go-detail{
  padding: 20px 0 20px 36px;
}
.gift-wrapper .giftOption{
  margin-bottom: 38px;
}
.gift-wrapper .inActive-giftOption .go-detail ul li{
  display: -ms-flexbox;
  display: flex;
  padding: 0;
  padding-bottom: 6px;
}
.gift-wrapper .inActive-giftOption .go-detail ul li button{
  width: 40px;
  height: 42px;
  border: none;
  background: #fe9e6f;
  color: #fff;  
  font-size: 14px;
  cursor: pointer;
}
.gift-wrapper .inActive-giftOption .go-detail ul li p{
  margin-bottom: 0;
  line-height: 40px;
  padding-left: 16px;
}
/* Switch Checkbox */

.gift-wrapper .go-as li input[type=checkbox]{
	height: 0;
	width: 0;
	visibility: hidden;
}

.gift-wrapper .go-as li label {
	cursor: pointer;
	text-indent: -9999px;
	width: 52px;
	height: 28px;
	background: #fe986d;
	border-radius: 100px;
  position: relative;
  margin-bottom: 0;
  margin: 0 auto;
}

.gift-wrapper .go-as li label:after {
	content: '';
	position: absolute;
	top: 1px;
	left: 1px;
	width: 26px;
	height: 26px;
	background: #fff;
	border-radius: 90px;
	-webkit-transition: 0.3s;
	-o-transition: 0.3s;
	transition: 0.3s;
}

.gift-wrapper .go-as li input:checked + label {
	background: #bada55;
}

.gift-wrapper .go-as li input:checked + label:after {
	left: calc(100% - 1px);
	-webkit-transform: translateX(-100%);
	    -ms-transform: translateX(-100%);
	        transform: translateX(-100%);
}

.gift-wrapper .go-as li label:active:after {
	width: 26px;
}

/* Responsive */
@media screen and (max-width: 920px){
  .gs-paymentgs .card-block{
    padding: 30px 40px 46px;
  }
  .gift-wrapper .card-header .title, .gift-wrapper .card-block .go-detail{
    min-width: 70%;
  }
  .gift-wrapper .card-header .activeStatus, .gift-wrapper .card-block .go-as{
    min-width: 30%;
  }
}
@media screen and (max-width: 641px){
  .gift-wrapper .card-header .title, .gift-wrapper .card-block .go-detail{
    min-width: 322px;
  }
  .gift-wrapper .card-header .activeStatus, .gift-wrapper .card-block .go-as{
    min-width: 138px;
  }
  /* .gift-wrapper .card-block .go-detail{
    overflow:auto;
  } */
}
@media screen and (max-width: 526px){
  .gift-wrapper .gift-addBtn .btn-red{
    margin-left: 0;
    margin-top: 16px;
  }
  .gift-wrapper .gift-addBtn .btn-red, .giftSetting .gift-addBtn .btn-gray{
    display: block;
    width: 100%;
  }
}
@media screen and (max-width: 450px){
  .gs-paymentgs .card-block{
    padding: 30px 20px 46px;
  }
}
@media screen and (max-width: 370px){
  .gs-paymentgs .card-header .title p{
    line-height: 22px;
    padding-top: 10px;
  }
}


